@font-face {
  font-family: iranyekan;
  src: url("./assets/fonts/IRANYekan.ttf");
}

*{
  font-family: iranyekan, serif;
  color: white;
  transition: 1s;
}

#about-us {
  background-color: black;
}

#title {
  font-weight: bold;
  font-size: 2.5vw;
  width: 15vw;
  text-align: center;
  line-height: 125%;
}

#description {
  font-size: 1vw;
  width: 32vw;
  text-align: right;
  direction: rtl;
}

#texts {
  padding-top: 15vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 15vw;
}

#logo {
  position: absolute;
  margin-top: 3vw;
  margin-left: 90vw;
  width: 4.5vw;
}

::-webkit-scrollbar {
  width: 15px;
  height: 15px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 10 10 10px black;
  background: black;
}

::-webkit-scrollbar-thumb {
  background: #C4C4C4;
  border-radius: 25px;
  border: 4px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
}

::-webkit-scrollbar-thumb:hover {
  background: #4286F5;
  border: 4px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
}


@media screen and (max-width: 600px){
  #about-us {
    display: flex;
    flex-direction:  column;
  }

  #texts {
    margin-top: 15vw;
    flex-direction: column-reverse;
  }

  #title {
    font-size: 5.5vw;
    width: 70vw;
  }

  #description {
    width: 75vw;
    font-size: 4.3vw;
  }

  #logo {
    margin-top: 10vw;
    width: 16vw;
    margin-left: 75vw;
  }
}

#mobile-menu {
  background-color: black;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  z-index: 5000;
  display: none;
}

@media screen and (min-width: 600px){
  #mobile-menu {
    display: none;
  }
}

.my-button {
  position: absolute;
  margin-top: 2vw;
  z-index: 1000;
  margin-left: 5vw;
}

.menu {
  padding-top: 5vw;
  background-color: #0A0A0A;
  z-index: 5;
}

.items {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
